// websocket
// 

import login from '../store/login/index'

export const GET_TIME_ERROR = 124;
export const GET_TIMEOUT = 125;
export const CONNECT_END = 126;
export const NO_MATCH = 127;

const defaultTimeout = 15000; // 

class WsMQ {
  // path -- 
  // hostfun -- 
  // openfun -- (Option)
  // closefun -- (Option)
  constructor(path, hostfun, openfun, closefun) {
    // openfun 
    const ts = this;
    ts.path = path;
    ts.hostfun = hostfun;
    ts.id = Math.floor(Math.random() * 9999) + 1;
    ts.last = 0; // ping
    ts.conn = null;
    ts.openfun = openfun || (async () => { });
    ts.closefun = closefun || (async () => { });
    ts.cmds = {}; // id,  (state, data)
    ts.registers = {}; // cmd, data, backfun
    ts.willsend = []; // 
    ts.timer = {}; // 
    // 
    ts.connect();

    // 
    setInterval(() => {
      if (!ts.conn) {
        ts.connect();
        return;
      }
      const t = new Date().getTime();
      if (ts.last === 0) {
        return;
      }
      if (t - ts.last > 180000) {
        // 
        ts.conn.close();
        ts.conn = null;
        ts.last = 0;
        setTimeout(() => {
          ts.connect();
        }, Math.floor(Math.random() * 2000) + 1000);
        return;
      }
      if (t - ts.last > 80000) {
        // 
        ts.send({ id: ts.getid() });
      }
    }, 60000);
  }

  getid() {
    if (this.id <= 0) {
      this.id = 1;
    } else {
      this.id += 1;
    }
    return this.id;
  }

  send(data) {
    if (this.last > 0 && this.conn) {
      try {
        this.conn.send(JSON.stringify(data));
      } catch (er) {
        this.willsend.push(data);
      }
    } else {
      this.willsend.push(data);
    }
  }

  // 
  connect(host) {
    const ts = this;
    if (ts.conn) {
      // 
      return;
    }
    // 
    const lastTime = +new Date()
    if (ts.lastConnectTime && lastTime - ts.lastConnectTime < 15000) {
      setTimeout(() => {
        ts.connect();
      }, 8000);
      return
    }
    ts.lastConnectTime = lastTime
    if (!host) {
      host = ts.hostfun()
    }
    if (!host) {
      // 
      setTimeout(() => {
        ts.connect();
      }, 3000);
      return;
    }
    if (host.port) {
      ts.port = host.port
    } else {
      const ps = host.https_ports.split('|')
      // ts.port = ps[0] // 
      ts.port = ps[Math.floor(Math.random() * ps.length)] // 
    }
    ts.host = host
    // 
    for (let i = 0; i < 99; i++) {
      try {
        const url = `wss://${host.domain}:${ts.port}${(typeof ts.path === "function") ? ts.path() : ts.path}`;
        ts.conn = new WebSocket(url); // url
        // console.log("[CONNECT]", url)
        host.port = ts.port
        break
      } catch (ex) {
        const ps = host.https_ports.split('|')
        ts.port = ps[Math.floor(Math.random() * ps.length)] // 
      }
    }
    if (!ts.conn) {
      return
    }
    // 10
    ts.connectTimer = setTimeout(() => {
      ts.connectTimer = null
      ts.close()
    }, 10000)
    ts.conn.onclose = () => {
      ts.conn = null;
      ts.last = 0;
      ts.closefun(ts.host);
      setTimeout(() => {
        ts.connect(); // 
      }, Math.floor(Math.random() * 2000) + 1000);
    };
    ts.conn.onmessage = evt => {
      ts.last = new Date().getTime();
      try {
        const msg = JSON.parse(evt.data);
        if (msg.cmd) {
          // 
          console.log('[WSIN]', msg)
          login.getUser_detail();
          const backfun = ts.registers[msg.cmd];
          if (backfun) {
            backfun(msg.data, (state, result) => {
              // msg.id 0 
              if (msg.id === 0) {
                return;
              }
              const dx = {
                id: msg.id,
                state,
              };
              if (result !== null && result !== undefined) {
                dx.data = result;
              }
              ts.send(dx);
            });
          } else if (msg.id !== 0) {
            ts.send({
              id: msg.id,
              state: NO_MATCH,
            });
          }
        } else if ('state' in msg) {
          // 
          const tcmd = ts.cmds[msg.id];
          if (tcmd) {
            delete ts.cmds[msg.id];
            if (ts.timer[msg.id]) {
              clearTimeout(ts.timer[msg.id]);
              delete ts.timer[msg.id]
            }
            tcmd(msg.state, msg.data);
          }
        }
      } catch (ex) {
        console.log(ex)
        return
      }
    };
    // 
    ts.conn.onopen = () => {
      // 
      if (ts.connectTimer) {
        clearTimeout(ts.connectTimer)
        ts.connectTimer = null
      }
      ts.last = new Date().getTime();
      ts.openfun(ts.host).then(() => {
        ts.willsend.forEach(data => {
          ts.send(data)
        })
        ts.willsend = []
      });
    };
  }

  // (string, function(data, (state, result)=>{}))
  register(cmd, backfun) {
    this.registers[cmd] = backfun;
  }

  // backfun
  get(cmd, data, backfun, timeout = defaultTimeout) {
    const ts = this;
    if (!ts.conn) {
      if (backfun) {
        backfun(CONNECT_END);
      }
      return;
    }
    let id = 0;
    if (backfun && timeout > 0) {
      id = ts.getid();
      ts.cmds[id] = backfun;
      // 
      ts.timer[id] = setTimeout(() => {
        delete ts.timer[id]
        const bf = ts.cmds[id];
        if (bf) {
          delete ts.cmds[id];
          bf(GET_TIMEOUT);
        }
      }, timeout);
    }
    const dx = { id, cmd };
    if (data !== null && data !== undefined) {
      dx.data = data;
    }
    ts.send(dx);
  }

  // 
  geturl(pre = 'https') {
    const ts = this
    return ts.conn && ts.host ? `${pre}://${ts.host.domain}:${ts.port}` : '';
  }

  // 
  gethost() {
    return this.conn && this.connhost ? this.connhost : null;
  }

  // 
  setpath(path, delay = 1) {
    const ts = this
    ts.path = path
    ts.close()
    setTimeout(() => {
      ts.connect(ts.host);
    }, delay);
  }

  // 
  // host 
  reconnect(host, delay = 1) {
    const ts = this
    ts.close()
    setTimeout(() => {
      ts.connect(host);
    }, delay);
  }

  // 
  close() {
    const ts = this
    if (ts.conn) {
      ts.conn.close()
      ts.conn = null
    } else {
      ts.closefun(ts.host);
    }
    ts.last = 0
  }

  // 
  isconnected() {
    return (!!this.conn) && (this.last > 0)
  }
}

export default WsMQ;
